* {
  margin: 0;
  box-sizing: border-box;
  padding: 0;
  font-family: 'PT Sans', sans-serif;
}
.btn__primary {
  padding: 8px 20px;
  border-radius: 20px;
  outline: none;
  border: none;
  cursor: pointer;
  white-space: nowrap;
  background-color: #E40544;
  color: #fff;
  border: 1px solid #E40544;
  font-weight: bold;
}

.btn__outline {
  background-color: transparent;
  color: #fff;
  padding: 8px 20px;
  border: 1px solid #fff;
  transition: all 0.3s ease-out;
}

.btn__medium {
  font-size: 18px;
}

.btn__large {
  padding: 12px 26px;
  font-size: 20px;
}

.btn__large:hover, .btn__medium:hover {
  transition: all 0.3s ease-out;
}

.btn__outline:hover {
  background-color: #fff;
  color: #242424;
  transition: all 0.3s ease-out;
}

.btn__primary:hover {
  background-color: transparent;
  color: #fff;
}
.hero__section {
	padding: 120px 100px ;
	color: #fff;
}

.hero__row {
	align-items: center;
	display: flex;
	justify-content: center;
	flex-direction: row;
}

.row {
	display: flex;
	margin-right: -15px;
	margin-left: -15px;
	flex-wrap: wrap;
	align-content: stretch;
}

.col {
	margin-bottom: 15px;
	padding: 0 15px;
	flex: 1 1;
	max-width: 50%;
	flex-basis: 10%;
}

.hero__topLine {
	color: #f00946;
	font-size: 16px;
	line-height: 16px;
	font-weight: 700;
	letter-spacing: 1.4px;
	text-transform: uppercase;
	margin-bottom: 16px;
}

.hero__heading {
	margin-bottom: 24px;
	font-size: 34px;
	line-height: 1.1;
	font-weight: 600;
}

.hero__subtitle {
	max-width: 440px;
	margin-top: 12px;
	margin-bottom: 35px;
	font-size: 16px;
	line-height: 24px;
}

.hero__img {
	display: flex;
	justify-content: center;
	max-width: 95%;
	margin:  0px 10px 0px 10px;
}

.newsletter__input {
	width: 90%;
	display: flex;
	align-items: center;
	height: 50px;
}

.newsletter__container {
	display: flex;
	align-items: center;
	border-radius: 50px;
	border:  1px solid lightgray;
	padding-left: 20px;
	width:  500px;
	height: 100%;
}

.newsletter__container > input {
	font-size: 16px;
	padding-left: 15px;
	outline: none;
	border: none;
	margin-right: auto;
}

.newsletter__button {
	margin-right: 5px;
	padding: 8px 12px;
	height: 80%;
	border-radius: 50px;
	outline: none;
	border: none;
	cursor: pointer;
	white-space: nowrap;
	background-color: transparent;
	color: #1C2237;
	border: 1px solid #E40544;
	font-weight: bold;
	font-size: 12px;
}


.icon {
	color: #E40544;
}

.newsletter__button:hover {
  background-color: #E40544;
  color: #fff;
}

img {
	vertical-align: middle;
	width: 450px;
}

.hero__darkBg {
  background-color: #1c2237;
}

.dark {
  color: #1c2237;
}
.staking {
	padding: 64px 100px ;
	color: #fff;
}

.staking__title {
	margin-bottom: 60px;
	line-height: 40px;
	color: #333;
	text-align: center;
}

.staking__row {
	align-items: center;
	display: flex;
	flex-direction: row;
	justify-content: center;
}

.row {
	display: flex;
	margin-right: -15px;
	margin-left: -15px;
	flex-wrap: wrap;
	align-content: stretch;
}

.stake__col {
	margin:  0px 25px;
	flex: 1 1;
	max-width: 40%;
	flex-basis: 10%;
}

.staking__textWrapper {
	padding: 30px 40px;
	max-width: 100%;
	border: 1px solid lightgray;
	border-radius: 40px;
	background-color: #1c2237;
}

.col__stats {
	line-height: 25px;
	margin-bottom: 15px;
	padding-left: 10px;
}

.staking__input {
	flex: 1 1;
	border: none;
	border-radius: 10px;
	padding: 8px 12px;
	margin: 10px  0px 20px 0px;
}

.icon__wrapper {
	display: flex;
	flex-direction: row;
	align-items: center;
}

.staking__inputHeader {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: baseline;
	margin-top: 10px;
}

.staking__inputBox {
	display: flex;
}

.staking__submit {
	display: flex;
	margin-bottom: 10px;
}

.staking__colInfo  > p {
	font-size: 12px;
}

