}

span {
	font-size: 14px;
	font-weight: normal;
}

.stake__button {
  padding: 8px 24px;
  margin: 0px 2px;
  border-radius: 20px;
  outline: none;
  border: none;
  cursor: pointer;
  white-space: nowrap;
  background-color: #E40544;
  color: #fff;
  border: 1px solid #E40544;
  font-weight: bold;
  flex: 1 1;
}

.unstake__button {
  padding: 8px 24px;
  margin: 0px 2px;
  border-radius: 20px;
  outline: none;
  border: none;
  cursor: pointer;
  white-space: nowrap;
  background-color: transparent;
  color: #fff;
  border: 1px solid #E40544;
  font-weight: bold;
  flex: 1 1;
}

.stake__button:hover {
  background-color: transparent;
}

.unstake__button:hover {
  background-color: #E40544;
}

input[type=number]:focus {
	outline: none;
}
.footer {
	margin-top: 50px;
	background: #1c2237;
	height: 60px;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	font-size: 1.2rem;
	padding: 20px;
	top: 0;
	position: -webkit-sticky;
	position: sticky;
}


.footer__left{
	margin-right: auto;
}

.footer__right {
	display: flex;
	align-items: center;
}

.footer__socialSection {
	display: flex;
}

.footer__icon {
	color: #fff;
	cursor: pointer;
	text-decoration: none;
	display: flex;
	align-items: center;
	height: 25px;
	margin-right: 10px;
	object-fit: contain;
}

.footer__links {
	display: flex;
	align-items: center;
	padding: 0px 10px;
	color: #fff;
	text-decoration: none;
	font-size: 16px;
}

.navbar {
	background: #1c2237;
	height: 80px;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	font-size: 1.2rem;
	padding: 40px;
	top: 0;
	position: -webkit-sticky;
	position: sticky;
}


.navbar__left{
	margin-right: auto;
}

.navbar__right {
	display: flex;
	align-items: center;
}

.navbar__logo {
	color: #fff;
	cursor: pointer;
	text-decoration: none;
	display: flex;
	align-items: center;
	height: 25px;
	object-fit: contain;
}

.nav__links {
	padding: 0px 10px;
	color: #fff;
	text-decoration: none;
}
